<template>
	<view class="tabbar-list cu-bar tabbar bg-black">
		<view class="action text-white" :class="0 == navCur ? 'text-orange' : '' " @tap="navChange" data-cur="0">
			<view class="cuIcon-homefill"></view> 首页
		</view>
		<view class="action text-white" :class="1 == navCur ? 'text-orange' : ''" @tap="navChange" data-cur="1">
			<view class="cuIcon-similar"></view> 学习
		</view>
		<view class="action text-white add-action" :class="2 == navCur ? 'text-orange ' : ''" @tap="navChange"
			data-cur="2">
			<button class="cu-btn cuIcon-friendfill bg-white shadow" :class="2 == navCur ? 'bg-orange ' : ''"></button>
			社区
		</view>
		<view class="action text-white" :class="3 == navCur ? 'text-orange' : ''" @tap="navChange" data-cur="3">
			<view class="cuIcon-video"></view> 风采
		</view>
		<view class="action text-white" :class="4 == navCur ? 'text-orange' : ''" @tap="navChange" data-cur="4">
			<view class="cuIcon-my"></view> 我的
		</view>
	</view>
</template>

<script>
	export default {
		name: "tabbar-list",
		data() {
			return {
				navCur: 0
			};
		},
		created() {
			this.navCur = this.pickIndex
		},
		props: {
			pickIndex:{
				type:Number,
				required:true
			}
		},
		methods: {
			/**
			 * tabbar切换
			 */
			navChange(e) {
				// console.log(e)
				this.navCur = e.currentTarget.dataset.cur;
				this.$emit('selected',{
					navCur:this.navCur
				})
			}
		}

	}
</script>

<style lang="scss" scoped>
	.tabbar-list {
		position: fixed;
		height: 50px;
		bottom: 0;
		width: 100%;
		background-color: #535151;
	}
	.bg-orange{
		background-color: rgba(255, 141, 26, 1);
	}
	.text-orange{
		// color: #f37b1d;
		color: rgba(255, 141, 26, 1);
	}
</style>
